.promo {
    margin-bottom: 40px;
    position: relative;
}

.promo__content {
    background-color: rgba(255,218,236,255);
    border-radius: 59px;

    justify-content: center;

    display: flex;
    column-gap: 250px;

    position: relative;
}

.promoImg{
    border-radius: 50px;
}

.promo__title {
    font-weight: 900;
    font-size: 45px;
    line-height: 2;
    color: rgb(37, 150, 190);

    padding-top: 100px;
}

.promo__desc {
    font-weight: 300;
    font-size: 30px;
    line-height: 1.5;
    color: rgb(37, 150, 190);
    position: relative;

    padding-top: 50px;
    /*max-width: 650px;*/
}


@media screen and (max-width: 480px){
    .promo__content{
        column-gap: normal;

    }
}